:root {
    --iu-crimson: #990000;
    --iu-cream: #EDEBEB;
    --iu-dark-midnight: #01426A;
    --iu-midnight: #006298;
    --iu-dark-majestic: #512A44;
    --iu-majestic: #66435A;
    --iu-black: #191919;
    --iu-mahogany: #4A3C31;
    --iu-alert-default-background: #EEE;
    --iu-alert-default-border: #A7A9AB;
    --iu-alert-message-background: #FDF7E7;
    --iu-alert-message-border: #F5BB17;
    --iu-alert-success-background: #DDF4EA;
    --iu-alert-succss-border: #009933;
    --iu-alert-error-background: #FDEEE8;
    --iu-alert-error-border: #F25B19;
    --iu-alert-info-background: #E9F6FC;
    --iu-alert-info-border: #49AFC7;
}

.navbar-background-custom {
    background-color: var(--iu-crimson);
}

.navbar-brand {
    color: var(--iu-cream) !important;
}

.custom-nav-button {
    min-width:40%;
}

.footer-background-custom {
    background-color: var(--iu-crimson);
    color: var(--iu-cream);
}

#userInput {
    width:100%;
}

.btn-dark {
    background-color: var(--iu-midnight) !important;
    border-color: var(--iu-midnight) !important;
}

body {
    font-size: 20px;
}

input {
    border: 1px solid;
    text-align: right;
}